import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import store from './store'//vue
import './testjichu';

Vue.config.productionTip = false

import utils from '../xp/js/utils'
utils.apDirective(Vue)
Vue.prototype.$utils = utils
import jiami from'../xp/js/jiami'
jiami.apDirective(Vue)

import user from './assets/user'
Vue.prototype.$user = user

import echarts from 'echarts'
Vue.prototype.$echarts = echarts

import apGglobal from 'ap-zhou-components'
Vue.use(apGglobal)

import VueI18n from 'vue-i18n'
Vue.use(VueI18n) // 

// PDF
import pdfFile from '@/components/project_subject/admin_istrative/qianzhiGZ/index'
Vue.prototype.$pdfFile = pdfFile

const messages = {
  zh: {
    message: {
      hello: ''
    }
  },
  en: {
    message: {
      hello: 'good good study, day day up!'
    }
  }
}
const i18n = new VueI18n({
    locale: 'zh',    // 
    //this.$i18n.locale // locale
    messages
})
export default i18n

Vue.prototype.axios = axios
import apn from '../config/transfer-station.js'
axios.defaults.baseURL = apn.main
//	   
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';  
// axios.defaults.withCredentials = true


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  store,
  components: { App },
  template: '<App/>'
})



