export default {
    state:{
    	headShow:true,
    	sideShow:true,
    	height:document.body.offsetHeight,
    	width:document.body.offsetWidth
    },
    mutations:{// 
    },
    actions:{//
    },
    getters:{//
    	bodyWidth:function(state){
    		return state.width
    	},
    	bodyHeight:function(state){
    		return state.height
    	},
       	
       	
    },
}



