<template>
    <div>
      <ap-pdf :menuPdfData='pdf.pdfObj' v-if="pdf.pdfShow" @openpdf="hidePdf"></ap-pdf>
      <p @click="openpdf(obj)">pdf</p>
    </div>
</template>

<script>
import apPdf from './index'
export default {
   data() {
     return {
        pdf:{//
          pdfShow:false,
          pdfObj:null,
          obj: null
        },
     }
   },
   components: {
     apPdf
   },
   methods: {
     //************************
			openpdf(obj){//
				this.pdf.pdfObj=obj[0]
				this.pdf.pdfShow=true
			},
			hidePdf(){//
				this.pdf.pdfShow=false
				this.pdf.pdfObj=null
				
			},
   },
   created() {
     this.obj = [
        {
          fileSuffix: "docx",
          // fileSuffix: "png",
          fileId: 11511,
          fileName: ".docx"
        }
     ]
   } 
}
</script>

<style>

</style>



