<!-- 
dataSource: <[]>  //   id
init(data): <function> // 
data<{}>
{
  key: 
  morenId: 
  disableId: ,
  ifClear:   // true
}
updataList<function(a, b, c)>  //  a b c 

checkedAllMsg:     
borrowDetails vuex
bug:
1.    
 -->
<template>
  <div id="cerManageSelect" v-if="isShow">
    <div class="cerManageSelect_cant">
      <div class="cms_batch">
        <span v-if="isShowNO == 1"></span>
        <div class="cms_batch_right" v-if="isShowNO == 1">
          <input placeholder="" v-model="value" size="small" style="width:200px;" @input="getInitData">
          <button @click="search"></button>
        </div>
      </div>
      <div class="cms_table" v-if="isShowNO == 1">
        <div class="ap-table">
          <div class="table-header">
            <span class="check_box" :class="{checked: ifCheckAll}" @click="selectAllItem">
              <i class="iconfont icon-finish"></i>
            </span>
            <p style="width: 10%"></p>
            <p style="width: 20%"></p>
            <p style="width: 20%"></p>
            <p style="width: 15%"></p>
            <p style="width: 10%"></p>
            <p style="width: 10%"></p>
          </div>
          <div class="table-list" v-for="(list, index) of tableData" :key="index">
            <span class="check_box" :class="{checked: list.ifCheck==1, ban: list.ifCheck == -2}"
              @click="selectItem(list, index, tableData)">
              <i class="iconfont icon-finish"></i>
            </span>
            <p style="width: 10%">{{index + 1}}</p>
            <p style="width: 20%">{{list.text}}</p>
            <p :title="list.companyCredentialContent" style="width: 20%">
              {{list.companyCredentialContent | substring10}}
            </p>
            <p style="width: 15%">{{list.credentialUnit}}</p>
            <p style="width: 10%">{{list.place}}</p>
            <p style="width: 10%">
              <span v-if="list.dispositon == 1"></span>
              <span v-if="list.dispositon == 2"></span>
              <span v-if="list.dispositon == 3"></span>
            </p>
          </div>
        </div>
      </div>
      <div class="cms_fotter">
        <!--  4  -->
        <!--  <span>{{num}}</span> -->
        <div class="cms_fotter1">
          <button size="small" @click="close"></button>
          <button type="primary" size="small" @click="price"></button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {
    mapGetters
  } from 'vuex'
  export default {
    name: 'cerManageSelect',
    data() {
      return {
        // 
        isShow: false,
        key: 1,
        disableId: 1,
        ifClear: true, // 
        value: null,
        blur: {
          likeStr: null
        },

        isShowNO: 1,
        tableData: [],
        pa: {}, // 
        num: 0, // 

        // ======= 
        ifCheckAll: false,
        typeChoose: '',
        checkedAllMsg: [], // 
        defaultSelected: []
      }
    },
    props: {
      dataSource: {
        type: Array,
        default: () => []
      }
    },
    mounted() {
      // 
      for (let k in this.borrowDetails.table1) {
        this.defaultSelected.push(this.borrowDetails.table1[k].id)
      }
      this.num = this.checkedAllMsg.length
    },
    destroyed() {
      // 
      // 1.  vuex  
      this.blur.likeStr = null
    },
    watch: {
      key() { //     
        this.checkedAllMsg = []
        this.borrowDetails.table1 = []
      }
    },
    computed: {
      ...mapGetters({
        'borrowDetails': 'borrowDetails'
      })
    },
    filters: {
      substring10(val) {
        if (!val || val.length < 10) return val
        return val.substring(0, 10) + ""
      }
    },
    components: {},
    methods: {
      getInitData() { // 
        // if (!this.value) {
        //   this.blur.likeStr = this.value
        //   this.init()
        // }

      },
      search() {
        this.blur.likeStr = this.value
        this.init()
      },
      init(obj = {}) {
        this.isShow = true
        this.key = obj.key || 1
        this.defaultSelected = obj.defaultSelected || []
        this.disableId = obj.disableId || 2
        this.ifClear = obj.ifClear || true

        if (true) {
          this.tableData = [{
            "companyCredentialId": 85,
            "companyId": 5,
            "credentialUnit": "1",
            "companyCredentialContent": "",
            "dispositon": 1,
            "text": "1",
            "place": "1",
            "pics": []
          }, {
            "companyCredentialId": 84,
            "companyId": 5,
            "credentialUnit": "2",
            "companyCredentialContent": "",
            "dispositon": 1,
            "text": "1",
            "place": "1",
            "pics": []
          }, {
            "companyCredentialId": 55,
            "companyId": 5,
            "credentialUnit": "",
            "companyCredentialContent": "",
            "dispositon": 1,
            "text": "asdasdasd",
            "place": "1",
            "pics": []
          }, {
            "companyCredentialId": 54,
            "companyId": 5,
            "credentialUnit": "",
            "companyCredentialContent": "",
            "dispositon": 1,
            "text": "asdasdasd",
            "place": "1",
            "pics": [],
          }, {
            "companyCredentialId": 34,
            "companyId": 5,
            "credentialUnit": "",
            "companyCredentialContent": "465",
            "dispositon": 1,
            "text": "asdasdasd",
            "place": "1",
            "pics": [],
          }]
          // dispositon != 1 
          if (this.blur.likeStr) {
            this.tableData = [{
              "companyCredentialId": 34,
              "companyId": 5,
              "credentialUnit": "",
              "companyCredentialContent": "465",
              "dispositon": 1,
              "text": "asdasdasd",
              "place": "1",
              "pics": [],
            }]
            // this.tableData.forEach(item => {
            //   if(this.blur.likeStr.includes(item.credentialUnit)) {
            //     this.tableData.push(item) 
            //   }
            // })
          }
          let _delArr = []
          for (let k in this.tableData) {
            if (this.tableData[k].remindDays < 0) {
              _delArr.push(k)
            }
          }
          let _delArr2 = _delArr.reverse()
          for (let k in _delArr2) {
            this.tableData.splice(_delArr2[k], 1)
          }
          // vuex 
          this.defaultSelected = []
          for (let k = 0; k < this.borrowDetails.table1.length; k++) {
            this.defaultSelected.push(this.borrowDetails.table1[k].id)
          }

          for (let t = 0; t < this.tableData.length; t++) {
            this.tableData[t].ifCheck = -1
            // 
            for (let i = 0; i < this.defaultSelected.length; i++) {
              if (this.tableData[t].companyCredentialId == this.defaultSelected[i]) {
                this.tableData[t].ifCheck = 1
                // this.checkedAllMsg.push(this.tableData[t])
              }
            }
          }
        }
        // 
        // this.page = res.data.page
        // this.count = res.data.count
      },
      saveVuex(newAddArr) {
        this.num = 0
        if (this.isShowNO == 1) {
          this.pa.table1 = []
          for (let k = 0; k < newAddArr.length; k++) {
            this.pa.table1.push({
              borrowType: 1,
              id: newAddArr[k].companyCredentialId,
              isDisabled: false,
              dispositon: newAddArr[k].dispositon,
              typeVos: newAddArr[k].text,
              place: newAddArr[k].place,
              credentialForm: null,
              purpose: null,
              borrowDate: [],
              remark: ''
            })
          }
        }
      },
      arrayWeightRemoval(array1, array2) {
        var tempArray1 = []; //1
        var tempArray2 = []; //2
        for (var i = 0; i < array2.length; i++) {
          tempArray1[array2[i]] = true; //array2 tempArray1 true
        }
        for (var i = 0; i < array1.length; i++) {
          if (!tempArray1[array1[i]]) {
            tempArray2.push(array1[i]); //array1 array2 
          }
        }
        return tempArray2;
      },
      price(newPrice) {
        // this.checkedAllMsg = []
        //      
        // if (this.isShowNO == 1) {
        //   for (let k in this.tableData) {
        //     if (this.tableData[k].ifCheck == 1) {
        //       this.checkedAllMsg.push(this.tableData[k])
        //     }
        //   }
        // }
        let arr1 = [],
          arr2 = []
        if (this.isShowNO == 1) { //  companyCredentialId
          for (let k = 0; k < this.checkedAllMsg.length; k++) {
            arr1.push(this.checkedAllMsg[k].companyCredentialId)
            arr2 = this.defaultSelected
          }
        }

        // 
        var arr3 = arr2.filter(el => !arr1.includes(el))
        // 
        var arr4 = arr2.filter(el => !arr3.includes(el))
        // 
        var arr5 = arr1.filter(el => !arr4.includes(el))
        let newAddArr = []
        for (let t = 0; t < this.checkedAllMsg.length; t++) {
          if (this.isShowNO == 1) {
            for (let k = 0; k < arr5.length; k++) {
              if (arr5[k] == this.checkedAllMsg[t].companyCredentialId) {
                newAddArr.push(this.checkedAllMsg[t])
              }
            }
          }
        }
        this.saveVuex(newAddArr)
        if (this.isShowNO == 1) {
          this.$store.dispatch('borrowDetailsMessage', [this.pa.table1, 1])

        } else {
          this.$store.dispatch('borrowDetailsMessage', [this.pa.table2, 2])
        }
        // vuex
        for (let k in this.borrowDetails.table1) {
          for (let t in arr3) {
            if (this.borrowDetails.table1[k].id == arr3[t]) {
              this.borrowDetails.table1.splice(k, 1)
            }
          }
        }
        this.$emit('updataList', arr3, arr4, this.key, this.borrowDetails.table1) // 
        this.isShow = false
      },
      close() {
        this.isShow = false
      },
      selectAllItem() {
        this.ifCheckAll = !this.ifCheckAll
        if (this.ifCheckAll) {
          for (let k in this.tableData) {
            this.tableData[k].ifCheck = 1
            this.checkedAllMsg.push(this.tableData[k])
          }
        } else {
          for (let k in this.tableData) {
            this.tableData[k].ifCheck = -1
            this.checkedAllMsg = []
          }
        }

      },
      // ==========
      selectItem(item, index, listData) {
        if (item.ifCheck == -2) { // -2 
          return
        }
        if (this.typeChoose == 'radio') { // 
          return  //  return  [Violation] 'click' handler took 158ms  
        } else { // 
          if (item.ifCheck == 1) {
            item.ifCheck = -1
            let id = []
            if (this.ifClear) {
              this.checkedAllMsg.forEach(k => {
                id.push(k.companyCredentialId)
              })
              let _index = id.indexOf(item.companyCredentialId)  //  companyCredentialId id
              this.checkedAllMsg.splice(_index, 1)
            }
          } else {
            item.ifCheck = 1
            this.checkedAllMsg.push(item)
          }
        }
        //   ( + ) = 
        // for (let k in listData) {
        //   if (listData[k].ifCheck == 1) {
        //     this.checkedAllMsg.push(listData[k])
        //   }
        // }

        // UI
        if (this.checkedAllMsg.length == listData.length) {
          this.ifCheckAll = true
          this.ifCheckAll2 = true
        } else {
          this.ifCheckAll = false
          this.ifCheckAll2 = false
        }
        this.num = this.checkedAllMsg.length
        this.$forceUpdate() // item.ifCheck  
      }
    }

  }

</script>

<style scoped lang="scss">
  #cerManageSelect {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    z-index: 99;
  }

  .cerManageSelect_cant {
    position: absolute;
    height: 100%;
    top: 0;
    right: 0;
    background-color: white;
  }

  .cms_batch {
    width: 100%;
    height: 53px;
    line-height: 53px;
    border-bottom: solid 1px #e8e8e8;
    position: relative;
  }

  .cms_batch span {
    margin-left: 24px;
    font-size: 16px;
    color: #1890FF;
    font-weight: bold;
  }

  .cms_batch_right {
    position: absolute;
    top: 0;
    right: 16px;
  }

  .cms_table {
    height: -webkit-calc(100% - 135px);
    height: -moz-calc(100% - 135px);
    height: calc(100% - 135px);
    padding: 20px;
    padding-top: 5px;
    width: 1250px;
    box-sizing: border-box;
    overflow-y: auto;
  }

  .cms_fotter {
    width: 100%;
    height: 79px;
    border-top: solid 1px #e8e8e8;
    position: relative;
    line-height: 79px;
    text-indent: 32px;
  }

  .cms_fotter1 {
    position: absolute;
    right: 32px;
    top: 0;
  }

  /* ===================== */
  .ap-table {
    .table-header {
      display: flex;
      align-items: center;
      background-color: rgb(250, 250, 250);
      margin-bottom: 10px;

      p {
        font-weight: bolder
      }
    }

    .table-list,
    .table-header {
      display: flex;
      align-items: center;

      .check_box {
        border: 1px solid #409eff;
        display: inline-block;
        width: 14px;
        height: 14px;
        cursor: pointer;
        text-align: center;
        line-height: 12px;
        // transition: all 300ms;
        box-sizing: border-box;
        margin-left: 10px;
      }

      .check_box i {
        display: inline-block;
        font-size: 12px !important;
        color: #333;
        transform: scale(0);
      }

      .checked {
        background-color: #409eff;
      }

      .checked i {
        color: #fff;
        transform: scale(1);
        transition: all 0.3s;
      }

      .ban {
        background-color: #edf2fc;
        border-color: #dcdfe6;
      }

      .ban i {
        color: #c0c4cc;
        transform: scale(1);
      }

      p {
        width: 15%;
        font-size: 14px;
        color: #333;
        text-align: center;
        padding: 15px 6px;
        box-sizing: border-box;

        span {
          display: block;
          font-size: 14px;
          padding: 7px;
          border-bottom: 1px solid #333;

          &:last-child {
            border-bottom: 0
          }
        }
      }
    }

    .table-list:hover {
      background-color: rgba(245, 247, 250, 1)
    }
  }

</style>



